.user{width:100%;padding:.56rem 0 .62rem 0;}
.user .usermsg{text-align: center;background:#C6E2FF;color:#4D4D4D;}
.user .usermsg img{width:1.6rem;height:1.6rem;border-radius: 50%;}
.user .usermsg h3{font:.26rem/2 "";}
.user .usermsg p{font:.2rem/2 "";}
.user .usermsg p .fenge{padding:0 .3rem;}
.user ul{background:rgba(0,0,255,.1);padding-top:.2rem;}
.user ul li{padding:0 .1rem;border-bottom:.01rem solid #fff;}
.user ul li h4{font:.26rem/.36rem "";}
.user ul li p{font:.2rem/.3rem "";padding:0 0 .1rem .2rem;}